import React from "react"
import { Link } from "gatsby"
import { projectInfoList } from "../contents/project-info";

// <div className="is-size-3 is-hidden">Design interactive systems to enrich everyday human experience</div>
// <div className="is-size-4 is-hidden">Continually explore and learn how to design interactive systems to enrich everyday human experience</div>    


const fixedMethodList = ["A/B Testing", "Affinity Wall", "Co-Design", "Comparative Analysis", "Contextual Inquiry", "Field Observation", "Heuristic Evaluation", "Participatory Design", "Persona", "Scenario", "Semi-structured Interview", "Thematic Analysis", "Usability Testing"];

// "Application Log Analysis", 


const fixedTechnologyList = ["Angular", "CSS", "Google Analytics", "Google Cloud Platform", "HTML", "Javascript", "NoSQL", "Node.js", "PHP", "Python", "Qualtrics", "React", "SQL"];


//  conduct research and build systems to make meaningful changes—one by one.

// I am a Ph.D. candidate at the <a href="https://www.umich.edu/" target="_blank" rel="noopener noreferrer">University of Michigan</a> <a href="https://www.si.umich.edu/" target="_blank" rel="noopener noreferrer">School of Information</a>, advised by <a href="https://socialworldsresearch.org/si/ackerm" target="_blank" rel="noopener noreferrer">Dr. Mark S. Ackerman</a>.

const Introduction = () => (
  <div className="content has-text-justified">
    <div className="hero">
      <div className="hero-body">
        <p className="title">
          I conduct research and build systems to make meaningful changes—one by one.
        </p>
        <p className="subtitle">

        </p>
      </div>
    </div>
    
    <p>
    I have a PhD in Information Science from the <a href="https://www.umich.edu/" target="_blank" rel="noopener noreferrer">University of Michigan</a> <a href="https://www.si.umich.edu/" target="_blank" rel="noopener noreferrer">School of Information</a> (UMSI), where I worked under the supervision of <a href="https://socialworldsresearch.org/si/ackerm" target="_blank" rel="noopener noreferrer">Dr. Mark S. Ackerman</a>. My research primarily focuses on <span className="has-text-weight-semibold">Human-Computer Interaction (UX), Computer-Supported Cooperative Work, and Ubiquitous Computing</span>. I am interested in designing systems to empower people in their everyday pursuits, such as <span className="has-text-weight-semibold">learning, social interaction, and health</span>.
    </p>
    <p>I am <span className="has-text-weight-semibold">a developer and a researcher</span> who thinks critically about interactive system design through research and enjoys building systems that create an impact. The  <Link to={"/projects/"}>projects</Link> I have worked on have helped people with different lived experiences to:
    </p>
    <ul>
      <li>develop fine-motor skills</li>
      <li>make life-changing health decisions</li>
      <li>enhance interactions in knowledge-based online communities</li>
      <li>support knowledge curation and community building for minority entrepreneurs</li>
      <li>develop functional independence through privacy-aware patient-generated data sharing</li>
    </ul>

    <br />
    <div className="columns">
      <div className="column"><div className="is-size-4">Methods I practice</div><br />

        <div className="tags">
          {
            Array.from(new Set([].concat.apply([], projectInfoList.map((projectInfo) => { return projectInfo.method_list; })).sort())).filter((topic) => { return topic == undefined ? false : topic.length > 0; }).filter((topic) => {
              return fixedMethodList.includes(topic);
            }).map((topic, indexT) => {
              return <span className="tag is-medium" key={indexT}>{topic}</span>
            })
          }
        </div>
      </div>
      <div className="column"><div className="is-size-4">Technology I employ</div><br />
        <div className="tags">
          {
            Array.from(new Set([].concat.apply([], projectInfoList.map((projectInfo) => { return projectInfo.skill_list; })).sort())).filter((topic) => { return topic == undefined ? false : topic.length > 0; }).filter((topic) => {
              return fixedTechnologyList.includes(topic);
            }).map((topic, indexT) => {
              return <span className="tag is-medium" key={indexT}>{topic}</span>
            })
          }
        </div>
      </div>
    </div>


    <p>In addition to research and development, I enjoyed sharing knowledge through teaching and mentoring about user interface development, UX research, and programming (see <Link to={"/mentoring/"}>Mentorship/Teaching</Link>).</p>



    <div className="is-size-4">Background</div><br/>
    <p>
      I earned a BS and an MS in <a href="https://www.csie.ntu.edu.tw/" target="_blank" rel="noopener noreferrer">Computer Science and Information Engineering</a> from <a href="http://www.ntu.edu.tw/english/" target="_blank" rel="noopener noreferrer">National Taiwan University</a>, Taiwan, and an MS in Information from the University of Michigan. I am a member of the <a href="http://socialworldsresearch.org/" target="_blank" rel="noopener noreferrer">SocialWorlds research group</a> and the <a href="http://misc.si.umich.edu/" target="_blank" rel="noopener noreferrer">Michigan Interactive and Social Computing (MISC) group</a>.
    </p>

    <p className="is-hidden">
      [New][Developing]I am interested in designing systems to empower people with unique backgrounds in their everyday pursuits such as health and hobbies, with the goal of improving social interaction and their quality of life. My approach to design seeks to develop insights on system design based on a solid understanding of people' lived experiences and existing systems' strength and weakness.
    </p>
    <div className="is-size-4 is-hidden">Methods</div>
    <p className="is-hidden">
      I employ different methods to study human experience in both digital and physical environments, such as analyzing browsing history, observing occupational therapy sessions, conduct co-design sessions with patients, and conducting semi-structured interviews to understand patients, caregivers, and clinicians' care-receiving and caregiving experiences.
    </p>
    <div className="is-size-4 is-hidden">Approach: Research, Design, Build, Evaluate</div>
    <p className="is-hidden">
      Built on my understanding of the experiences of different stakeholders, I design, build, and evaluate interactive systems on desktop, mobile, and web platforms to explore the potentials of interactive systems to make people's everyday life better and exciting. I am privileged to have worked with domain experts such as software developers, occupational therapists, public health researchers, physicians, caregivers, and patients, to co-create better user experience.
    </p>

    <hr />






    <ul className="is-hidden">
      <li>Methods: Semi-structured Interview, Usability Testing, Participatory Design, Co-Design, Field Observation, Heuristics Evaluation, Survey, Application Log Analysis</li>
      <li>Skills: JavaScript, React, Node.js, Python, R, PHP, SQL, NoSQL (MongoDB), HTML, CSS, Qualtrics, Drupal</li>
    </ul>

    <div className="is-hidden">
      <p>
        My major research interests include Human-Computer Interaction, Computer-Supported Cooperative Work, Ubiquitous Computing and End-User Programming. I am interested in designing systems or tools to support people with diverse background who live within a smart environment full of intelligent everyday objects, as it offers a complex and rich illustration of how technology could augment and improve the everyday interaction among people. I employ different methods to study human experience and devise implications for interactive system design. I design, build, and evaluate interactive systems to make people's everyday life better and exciting. I am privileged to have worked with domain experts such as software developers, occupational therapists, public health researchers, physicians, and patients, to understand and co-create better user experience.
      </p>
      More specifically, I am interested in designing systems to support people with diverse background who live within a smart environment full of intelligent everyday objects, as it offers a complex and rich illustration of how technology could augment and improve the everyday interaction among people. I employed different methods to study understand human experience and devise implicationos for system design.


      the intersection of communities and Ubiquitous Computing where people with diverse background live their lives within a smart environment full of intelligent everyday objects, as it offers a complex and rich illustration of how technology could augment and improve the everyday interaction among people. <span style={{ display: "none" }}>Currently, I am investigating how to design sensor-based mobile platforms to support informal health care at home for people with severe injuries.</span>
    </div>





    <div className="has-text-justified is-hidden">Pei-Yao is a Ph.D. candidate in the <a href="https://www.umich.edu/" target="_blank" rel="noopener noreferrer">University of Michigan</a> <a href="https://www.si.umich.edu/" target="_blank" rel="noopener noreferrer">School of Information</a>, advised by <a href="http://socialworldsresearch.org/si/ackerm" target="_blank" rel="noopener noreferrer">Dr. Mark S. Ackerman</a>. He is a member of the <a href="http://socialworldsresearch.org/" target="_blank" rel="noopener noreferrer">SocialWorlds research group</a> and the <a href="http://misc.si.umich.edu/" target="_blank" rel="noopener noreferrer">Michigan Interactive and Social Computing (MISC) group</a>. His major research interests include Human-Computer Interaction, Computer-Supported Cooperative Work, Ubiquitous Computing and End-User Programming. More specifically, He is interested in the intersection of communities and Ubiquitous Computing where people with diverse background live their lives within a smart environment full of intelligent everyday objects, as it offers a complex and rich illustration of how technology could augment and improve the everyday interaction among people. <span style={{ display: "none" }}>Currently, he is investigating how we could design sensor-based mobile platforms to support informal health care at home for people with severe injuries.</span> He received BS and MS in <a href="https://www.csie.ntu.edu.tw/" target="_blank" rel="noopener noreferrer">Computer Science and Information Engineering</a> from <a href="http://www.ntu.edu.tw/english/" target="_blank" rel="noopener noreferrer">National Taiwan University</a>, Taiwan, and a MS in Information from the University of Michigan.</div>






    <div style={{ display: "none", textAlign: "justify" }}>He has assisted in teaching graduate and undergraduate courses as a
      graduate student instructor (GSI) at the School
      of Information.
      <div style={{ display: "none", textAlign: "justify" }}>
        <ul>
          <li>
            <a href="https://www.si.umich.edu/programs/courses/502" rel="noopener noreferrer">SI 502: Networked Computing: Storage,
              Communication, and Processing</a>, with
            <a href="http://www.dr-chuck.com/" target="_blank" rel="noopener noreferrer">Professor Charles (Chuck) Severance</a>
          </li>
          <li>
            <a href="https://www.si.umich.edu/programs/courses/110" target="_blank" rel="noopener noreferrer">SI 110: Introduction to Information
              Studies</a>, with
            <a href="http://www.jeff-mason.com/" target="_blank" rel="noopener noreferrer">Professor Jeff Mackie-Mason</a>
          </li>
        </ul>
      </div>
    </div>

  </div>
)

export default Introduction


//     <p>See my <Link to={"/projects/"}>list of projects and details</Link>.</p>

// <p>In addition to research, I continuously broaden and deepen my knowledge and skills through online learning resources.  See collections of <a href="https://github.com/peiyaoh/programming-learning-resources" target="_blank" rel="noopener noreferrer">Programming</a> and <a href="https://github.com/peiyaoh/hci-ux-learning-resources" target="_blank" rel="noopener noreferrer">HCI/UX</a> resources I have curated for myself, colleagues, and students.</p>


